.snackbar-container {
	left: 50%;
	bottom: 0;
	z-index: 3;
	width: 60%;
	height: 0px;
	margin-left: -30%;
	line-height: 50px;
	position: fixed;
    background-color: var(--cor-primaria);
	border-radius: 5px;
	transition: all .3s;
	overflow: hidden;
}

.snackbar-container > span {
	border: 0;
	float: left;
	color: white;
	font-size: 15px;
	margin-left: 3%;
	background: transparent;
	text-transform: uppercase;
}

.snackbar-container > button {
    border: none;
    margin-right: 10px;
	float: right;
	color: white;
	cursor: pointer;
    font: 20px;
	position: relative;
	background: transparent;
}

@media screen and (max-width: 580px) {
	.snackbar-container {
		margin-left: -45%;
		width: 90%;
	}
}